@import "sass/index";

$fontFamily: "Varela Round";

.root {
  position: relative;
}

.input {
  width: 100%;
  color: use(--template-input-topLabel-color);
  border-radius: 8px;
  font-family: $fontFamily;
  font-size: 18px;
  padding: 11px;
  height: 45px;
  background: rgba(255, 255, 255, 0.05);
  & > input {
    padding: 0;
  }
}

.inputDisabled {
  color: use(--template-input-topLabel-color, 0.4);
}

.topLabel {
  margin-bottom: 4px;
  color: use(--template-input-topLabel-color);
  font-family: "Gilroy";
  font-weight: bold;
  & > label {
    display: inline-block;
    width: 50%;
  }
  & > span {
    display: inline-block;
    width: 50%;
    text-align: end;
  }
}

.coin {
  position: static;
  width: 26px;
  height: 26px;
  user-select: none;
}

.refreshIcon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

div.desktopButtons {
  @media (max-width: 380px) {
    display: none;
  }
}

.buttons {
  & > button {
    height: 30px;
    font-family: "Gilroy";
    font-size: 12px;
    font-weight: bold;
    color: use(--template-input-topLabel-color);
    width: 40px;
    &:not(:last-child) {
      margin-right: 2px;
    }
    &:hover {
      background: #5e2993;
    }
  }
}

.textAdornment {
  color: use(--template-input-topLabel-color, 0.5);
  font-family: $fontFamily;
  font-size: 13px;
}

.mobileButtons {
  display: none;
  justify-content: space-around;
  & > button {
    width: 100%;
    margin-top: 2px;
  }
  @media (max-width: 380px) {
    display: flex;
  }
}
